@font-face {
  font-family: 'Sofia_khizhnia';
  src: url('../fonts/Sofia_khizhnia.eot?33o6nr');
  src: url('../fonts/Sofia_khizhnia.eot?33o6nr#iefix') format('embedded-opentype'),
  url('../fonts/Sofia_khizhnia.ttf?33o6nr') format('truetype'),
  url('../fonts/Sofia_khizhnia.woff?33o6nr') format('woff'),
  url('../fonts/Sofia_khizhnia.svg?33o6nr#Sofia_khizhnia') format('svg');
  font-weight: normal;
  font-style: normal;
}

hr {
  border: none;
  text-align: center;
  font-size: 38px;
  @media (max-width: 1450px) {
    font-size: 28px;
  }
  @media (max-width: 920px) {
    font-size: 20px;
  }
}

.icon, hr {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'Sofia_khizhnia' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.ic-divider:before, hr:before {
  content: "\e900";
}

.ic-fb:before {
  content: "\e901";
}

.ic-instagram:before {
  content: "\e902";
}

.ic-ln:before {
  content: "\e903";
}

.ic-lupa:before {
  content: "\e904";
}

.ic-twitter:before {
  content: "\e905";
}

body, html {
  font-family: 'Roboto', sans-serif;
  font-size: 15px;
  line-height: 1.5;
  color: $text;
}

body {
  position: relative;
  background: $light-grey;
  overflow-x: hidden !important;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: $text;
  opacity: 0;
  visibility: hidden;
  @include transition(all, .3s, ease);
  z-index: 2;
  &[data-state='show'] {
    opacity: .7;
    visibility: visible;
  }
}

h3 {
  font-size: 23.28px;
}

.container {
  max-width: 1240px;
  width: 100%;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
  box-sizing: border-box;
}

%full-bg {
  width: 100vw;
  left: 50%;
  top: 0;
  margin-left: -50vw;
}

%button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearence: none;
  background: $main-color;
  color: $white;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  padding: 20px 84px;
  border: 2px solid $main-color;
  @include transition(all, .3s, ease);
  cursor: pointer;

  @media (max-width: 1450px) {
    padding: 10px 40px;
  }

  &:hover {
    background: transparent;
    color: $main-color;
  }
}

.button {
  display: inline-block;
  text-decoration: none;
}

button, .button {
  @extend %button;
}

.border-radius {
  border-radius: 35px;
}

.banner {
  position: relative;
  .full-img {
    position: absolute;
    height: 100%;
    @extend %full-bg;
    background-size: cover !important;
    background-position: center !important;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: black;
      opacity: .4;
    }
  }
  &-content {
    position: relative;
    z-index: 1;

    margin-left: auto;
    max-width: 600px;
    width: 100%;

    padding: 90px 0;

    display: flex;
    align-items: center;
    justify-content: flex-end;
    &-inner {
      margin-top: 20%;
      @media (max-width: 1450px) {
        font-size: 10%;
      }
      text-align: right;
      color: $white;
      font-family: 'Montserrat', sans-serif;
      h1 {
        font-size: 122.2px;
        @media (max-width: 1450px) {
          font-size: 90px;
        }
        @media (max-width: 720px) {
          font-size: 50px;
        }
        font-weight: bold;
        line-height: 1;
      }
      blockquote {
        font-weight: bold;
        font-size: 30px;
        @media (max-width: 1450px) {
          font-size: 22px;
        }
        @media (max-width: 1450px) {
          font-size: 18px;
        }
        margin-top: 20px;
      }
      .sub-title {
        font-family: 'Kaushan Script', cursive;
        font-size: 48px;
        @media (max-width: 1450px) {
          font-size: 38px;
        }
        @media (max-width: 1450px) {
          font-size: 30px;
        }
        margin-bottom: 35px;
      }
      .button {
        margin-top: 45px;
        font-size: 24px;
        @media (max-width: 1450px) {
          font-size: 18px;
        }
      }
    }
  }
}

.section {
  padding-top: 70px;
  padding-bottom: 70px;
  @media (max-width: 1450px) {
    padding-top: 45px;
    padding-bottom: 45px;
  }
}

.section-two-col {
  display: flex;
  @media (max-width: 1000px) {
    flex-direction: column;
  }
  .title-wr {
    width: 230px;
    @media (max-width: 1200px) {
      width: 180px;
    }
    padding: 12px;
    box-sizing: border-box;
    position: relative;

    display: flex;
    align-items: center;
    @media (max-width: 1000px) {
      width: 100%;
      padding-bottom: 40px;
    }
    img {
      opacity: .25;
      width: 100%;
      @media (max-width: 1000px) {
        display: none;
      }
    }
    &.small-img {
      img {
        max-width: 155px;
      }
    }
    .title {
      @media (min-width: 1001px) {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;

        display: flex;
        align-items: center;
        max-width: 115px;
      }
      width: 100%;
      h2 {
        font-family: 'Montserrat', sans-serif;
        font-weight: 700;
        font-size: 30px;
        @media (max-width: 1450px) {
          font-size: 25px;
        }
        text-transform: uppercase;
        text-align: center;
        @media (min-width: 1001px) {
          text-align: right;
        }
      }
    }
  }
  .content {
    display: flex;
    align-items: center;
    @media (min-width: 601px) {
      padding-left: 12px;
    }

    box-sizing: border-box;
    @include calc(width, '100% - 230px');
    @media (max-width: 1200px) {
      @include calc(width, '100% - 180px');
    }
    @media (max-width: 1000px) {
      width: 100%;
    }
  }
  &.large {
    .title-wr {
      justify-content: flex-end;
      width: 275px;
      @media (max-width: 1200px) {
        width: 180px;
      }
      @media (max-width: 1000px) {
        width: 100%;
      }
    }
  }

  &.reverse {
    flex-direction: row-reverse;
    @media (max-width: 1000px) {
      flex-direction: column;
    }
    .title-wr {
      @media (min-width: 1001px) {
        text-align: right;
      }
      .title {
        right: auto;
        left: 0;
      }
    }
    .content {
      padding-left: 0;
      padding-right: 12px;
    }
  }
}

#portfolio {
  position: relative;
  &:before {
    content: '';
    position: absolute;
    @extend %full-bg;
    height: 100%;
    background: url(../image/portfolio_img.jpg) no-repeat center;
    background-size: cover;
  }
  .portfolio-wrapper > * {
    position: relative;
    z-index: 1;
  }
  .title-wr {
    color: $white;
    img {
      opacity: .45;
    }
  }
  .content {
    color: $white;
    &-inner {
      width: 100%;
    }
  }
}

form {
  .error {
    display: none;
  }
  %textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    margin-bottom: 22px;
    box-sizing: border-box;
    border-radius: 0;

    &:focus {
      border-color: $main-color;
      outline: none;
      box-shadow: none;
    }
  }
  input {
    @extend %textarea;
    &.button {
      margin-bottom: 0;
      padding: 0;
      font-style: normal;
    }
  }
  input {
    height: 38px;
    border: 1px solid $black;
    padding: 0 10px;
    font-style: italic;
    color: $black;
  }
  .err {
    input {
      border-color: $red;
    }
  }
  textarea {
    @extend %textarea;

    border: 1px solid $black;
    padding: 10px;
    font-style: italic;
    min-height: 122px;
  }
}

/*owl slider*/
.owl-dots {
  position: relative;
  width: 100%;
  height: 35px;
  margin-top: 40px;
  @media (min-width: 921px) {
    margin-top: 0;
    position: absolute;
    bottom: 115px;
    right: -175px;
    transform: rotate(90deg);
  }
  @media (max-width: 800px) {
    height: 15px;
  }

  display: flex;
  justify-content: center;
  .owl-dot {
    height: 35px;
    width: 35px;
    border-radius: 35px;
    background: $white;
    margin: 0 11px;
    position: relative;
    z-index: 2;
    @media (max-width: 800px) {
      height: 15px;
      width: 15px;
      border-radius: 15px;
      margin: 0 5px;
    }

    &.active {
      background: $main-color;
    }
  }
  &:before {
    content: '';
    width: 100%;
    height: 2px;
    background: $white;
    position: absolute;
    left: 0;
    @include calc(top, '50% - 1px');
  }
}