.experience-year{
  padding-left: 205px;
  @media (max-width: 768px){
    padding-left: 175px;
  }
  @media (max-width: 600px){
    padding-left: 0;
  }
  box-sizing: border-box;

  > span{
    display: block;
    text-align: center;
    padding-bottom: 10px;
    font-size: 25px;
    font-weight: 500;
  }
  &:not(:first-child){
    > span{
      padding-top: 15px;
    }
  }
  .item{
    position: relative;
    .item-bullet{
      position: absolute;
      width: 20px;
      height: 20px;
      background: $light-grey;
      border: 2px solid #000;
      border-radius: 20px;
      left: -62.5px;
      @include calc(top, '50% - 10px');
      @media (max-width: 768px){
        left: -48.5px;
      }
      @media (max-width: 600px){
        display: none;
      }
      span{
        position: absolute;
        left: -145px;
        @media (max-width: 768px){
          left: -130px;
        }

        @include calc(top, '50% - 17px');

        display: inline-block;
        width: 88px;
        text-align: center;
        padding: 6px 10px;
        border: 2px solid $black;

        font-weight: 700;
        font-size: 18px;
        line-height: 1;
      }
    }

    padding: 25px 15px;
    border: 2px solid $black;

    &:not(:last-child){
      margin-bottom: 22px;
    }
    .vacancy, h4{
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
    }
    .info{
      font-weight: 500;
    }
  }
  &:not(:last-child){
    padding-bottom: 22px;
  }
  position: relative;
  &:before{
    content: '';
    position: absolute;
    height: 100%;
    width: 2px;
    background: $black;
    top:0;
    left: 155px;
    @media (max-width: 768px){
      left: 140px;
    }
    @media (max-width: 600px){
      display: none;
    }
  }
}