.footer-wrapper{
  .two-col{
    display: flex;
    width: 100%;
    @media (max-width: 650px){
      flex-direction: column-reverse;
    }
    .col{
      &:nth-child(1){
        width: 63%;
        @media (max-width: 920px){
          width: 55%;
        }
        @media (max-width: 650px){
          width: 100%;
        }
      }

      &:nth-child(2){
        width: 37%;
        @media (max-width: 920px){
          width:45%;
        }
        @media (max-width: 650px){
          width: 100%;
          margin-bottom: 40px;
          #map-canvas{
            height: 130px;
          }
        }
      }

    }
  }
  .form-wrapper{
    @media (min-width: 651px){
      padding: 0 30px;
    }
    box-sizing: border-box;
  }
  .map-wrapper{
    padding: 10px;
    box-sizing: border-box;
    border:1px solid #000;
  }
}

.social{
  margin-top: 35px;
  .icon{
    display: inline-block;
    font-size: 30px;
    &:not(:last-child){
      margin-right: 35px;
    }
    color: $main-color;
    @include transition(all, .3s, ease);
    &:hover{
      color: $black;
    }
  }
}
.copyright{
  border-top:1px solid #e5e5e5;
  color: #333;
  font-size: 14px;
  margin-top: 45px;
  padding: 20px 0;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
}

footer{
  padding-bottom: 0!important;
}