.portfolio{
  &_image{
   img{
     border: 2px solid $black;
     box-shadow: 5px 5px 0px 0px $main-color;
   }
  }
  &_items_list{
    margin-top: 28px;
    max-width: 700px;
    @media (max-width: 920px){
      max-width: 100%;
    }
    .portfolio_info{
      display: none;
    }
    .item{
        padding-right: 10px;
        padding-bottom: 10px;
        position: relative;
        cursor: pointer;
          &:before{
            font-family: 'Sofia_khizhnia';
            content: "\e904";
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top:0;
            left: 0;
            @include calc(width, "100% + 4px");
            height: 100%;
            background: rgba(0, 0, 0, 0.3);
            opacity: 0;
            visibility: hidden;
            color: $white;
            font-size: 30px;
          }
          &:hover{
            &:before{
              visibility: visible;
              opacity: 1;
            }
          }

        &:not(:last-child){
          margin-bottom: 38px;
        }

      }
    .owl-dots{
      @media (min-width: 921px){
        @include calc(width, '40% - 40px');
      }
    }
  }
  &_item_main{
    display: flex;
    @media (max-width: 800px){
      flex-direction: column;
      align-items: center;
    }
    .portfolio_image_outer{
      width: 40%;
      @media (max-width: 800px){
        padding-bottom: 28px;
        width: 290px;
      }
    }
    .portfolio_image{
      padding: 0 20px 15px 0;
      position: relative;

      img{
        width: 100%;
        box-shadow: 12px 12px 0px 3px #0fceaf;
      }
      &:before{
        content: '';
        position: absolute;
        @include calc(width, '100% - 18px');
        @include calc(height, '100% - 18px');
        top:0;
        left: 0;
        z-index: 1;
        background: rgba(0, 0, 0, 0.3);
        opacity: 0;
        visibility: hidden;
      }
      a{
        z-index: 2;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        @include calc(left, '50% - 88px');
        @include calc(top, '50% - 28px');
        width: 150px;
        padding: 5px 10px;
        text-align: center;
        font-size: 24px;
        @media (max-width: 600px){
          font-size: 18px;
        }
      }
      &:hover{
        a, &:before{
          opacity: 1;
          visibility: visible;
        }

      }
    }
    .portfolio_info{
      @include calc(padding-left, '10px + 2%');
      @include calc(padding-right, '10px + 2%');
      width: 60%;
      font-size: 15px;
      @media (max-width: 800px) {
        width: 100%;
        padding: 0;
      }

      line-height: 1.5;

      h2{
        font-weight: 700;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 1em;
        text-transform: uppercase;
      }
      p{
        + p{
          margin: .5em 0;
        }
      }
      ol, ul{
        margin: .5em 0 .5em 1em;
        padding-left: 1em;
      }
      ol{
        list-style-type: disc;
      }
      ul{
        list-style-type: decimal;
      }
    }

  }
}

