


#mobile-menu {
  position: fixed;
  top: 0;
  left: -80%;
  background: white;
  height: 100vh;
  width: 80%;
  z-index: 3;
  padding: 20px;
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
  display: none;
  @include transition(all, .3s, ease);
  &[data-state='show'] {
    left: 0;
    opacity: 1;
    visibility: visible;
  }
  ul{
    li{
      &.contact{
          margin-top: 10px;
      }
    }
  }
}

body {

  &.mobile, header.sticky {
    box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.24);
    nav {
      ul {
        li {
          a {
            color: $text;
          }
          &.contact {
            a {
              border-color: $text;
            }
            &.active, &:hover{
              a{
                border-color: $main-color;
              }
            }
          }
        }
      }
    }
  }
  header.sticky {
    background: $white;
  }

    nav {
      ul {
        li{
          a{
            font-family: 'Montserrat', sans-serif;
            font-weight: 600;
            font-size: 14px;
            color: $white;

            padding: 10px 15px;
            @include transition(all, .3s, ease);
            text-transform: uppercase;
            border: 2px solid transparent;
          }
          &.active, &:hover {
            a{
              color: $main-color!important;
            }

          }
          &:not(.contact) {
            a {
              position: relative;
              &:before {
                content: '';
                position: absolute;
                bottom: -3px;
                left: 15px;
                height: 2px;
                @include calc(width, '100% - 30px');
              }

            }
            &.active, &:hover {
              a {
                &:before {
                  background: $main-color;
                }
              }
            }
          }
          &.contact {
            a {
              border-color: $white;
            }
            &.active, &:hover{
              a{
                border-color: $main-color;
              }
            }
          }
        }
      }
    }
  &.mobile {
    header {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 2;
      background: $white;
      box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.24);

      .hamburger {
        margin-left: auto;
        display: block;
        width: 50px;
        height: 30px;
        padding: 10px 20px;
        cursor: pointer;
        span {
          width: 100%;
          height: 3px;
          background: $text;
          position: relative;
          display: block;
          top: 50%;
          &:before, &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            background: inherit;
            left: 0;
          }
          &:before {
            top: -10px;
          }
          &:after {
            top: 10px;
          }
        }
      }
    }
    nav {
      a {
        display: inline-block;
        &:before {
          display: none;
        }
      }
    }
  }

  &.desktop {
    header {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 2;
      .hamburger {
        display: none;
      }
      ul {
        padding: 30px 0;

        display: flex;
        justify-content: flex-end;
      }
    }

  }
}