.skill-wrapper{
  h3{
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    margin-bottom: 10px;
    @media (max-width: 1400px){
      margin-bottom: 9px;
      font-size: 20px;
    }
    @media (max-width: 920px){
      margin-bottom: 7px;
      font-size: 17px;
    }
    @media (max-width: 450px){
      margin-bottom: 5px;
      font-size: 15px;
    }
  }
  .slider{
    &-wr{
      height: 9px;
      margin-bottom: 35px;
      @media (max-width: 920px){
        height: 7px;
      }
      @media (max-width: 450px){
        height: 5px;
      }
      width: 100%;
      background: $black;
    }
    &-status{
      position: relative;
      background: $main-color;
      height: 9px;
      width: 0;
      @media (max-width: 920px){
        height: 7px;
      }
      @media (max-width: 450px){
        height: 5px;
      }
      &-count{
        position: absolute;
        background: $black;
        padding: 5px 8px;
        box-sizing: border-box;
        color: $white;
        line-height: 1;
        opacity: 0;

        font-size: 23.28px;
        @media (max-width: 1450px){
          font-size: 20px;
        }
        @media (max-width: 920px){
          font-size: 16px;
        }
        @media (max-width: 450px){
          font-size: 14px;
        }
        font-family: 'Roboto', sans-serif;
        font-weight: 500;


        bottom: 9px;
        @media (max-width: 920px){
          bottom: 7px;
        }
        @media (max-width: 450px){
          bottom: 5px;
        }
      }
    }
  }
  .two-col{
    display: flex;
    width: 100%;
    @media (max-width: 620px){
      flex-direction: column;
    }
    .col{
      width: 50%;
      padding-right: 100px;
      @media (max-width: 1000px){
        padding-right: 0;
        width: 43%;
        margin: 0 auto;
      }
      @media (max-width: 620px){
        width: 75%;
      }
      @media (max-width: 450px){
        width: 100%;
      }
      box-sizing: border-box;
      .items{
        &:not(:last-child){
          margin-bottom: 30px;
        }
      }
      &:not(:last-child){
        .items{
          @media (max-width: 620px){
            margin-bottom: 30px;
          }
        }
      }
    }
  }
}